.slide {
  height: 525px;
}
.wrapperLogo {
  height: 130px;
  position: relative;
}
.logo {
  position: absolute;
  z-index: 999;
  display: block;
  width: 155px;
  height: 155px;
  left: 50%;
  transform: translate(-50%, 0);
}
.textContainer {
  height: 395px;
  position: relative;
  border: 1px solid #8ecdf6;
}
.city {
  font-size: 22px;
  font-weight: 300;
  color: #222222;
  text-align: center;
  margin-top: 50px;
  text-transform: uppercase;
}
.wrapperList {
  margin-left: 30px;
  font-size: 14px;
  font-weight: 300;
  color: #222222;
  line-height: 1.5;
}
.direction {
  font-weight: 300;
  margin: 0;
}
.directionList {
  color: #69c9ca;
  padding-left: 13px;
  list-style-type: disc;
}
.directionListItemText {
  color: #222222;
}
.buttonItem {
  background: #fff;
  color: #69c9ca;
  border: 1px solid #69c9ca;
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  line-height: 1;
  padding: 14px 0;
  width: 155px;
  text-align: center;
  position: absolute;
  cursor: pointer;
  transition: all 0.3s ease;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%, 0);
}
.buttonItem:hover {
  background: #69c9ca;
  color: #fff;
}
.buttonItem:hover, .buttonItem:focus, .buttonItem:active {
  outline: none;
}
.backgroundImg {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.imgWrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}


@media(min-width: 1200px) {
  .slide {
    height: 619px;
  }
  .logo {
    width: 200px;
    height: 200px;
  }
  .wrapperLogo {
    height: 150px;
  }
  .textContainer {
    height: 469px;
  }
  .city {
    font-size: 48px;
    margin-top: 50px;
  }
  .wrapperList {
    font-size: 18px;
  }
}
