.section {
  height: 690px;
  background: #fcfafb;
  background-image: url('../../img/background/portfolioMobSomething.png');
  background-image: -webkit-image-set(url('../../img/background/portfolioMobSomething.webp') 1x, url('../../img/background/portfolioMobSomething.png') 1x);
  background-position: 0 0;
  background-size: 27% auto;
  background-repeat: no-repeat;
  clip-path: polygon(0% 0%, 50% 3%, 100% 0%, 100% 97%, 50% 100%, 0 97%);
  transform: translate(0, -36%);
  -ms-transform: translate(0, 0);
  padding-top: 40px;
}
.header {
  font-size: 30px;
  color: #0b4f50;
  text-transform: uppercase;
  font-family: 'Poiret One';
  text-align: center;
  line-height: 1;
  margin-bottom: 15px;
}

@media(min-width: 1200px) {
  .section {
    height: 886px;
    background-image: none;
    clip-path: polygon(0% 0%, 50% 6%, 100% 0%, 100% 94%, 50% 100%, 0 94%);
    transform: translate(0, -72%);
    -ms-transform: translate(0, 0);
    padding-top: 94px;
    position: relative;
  }
  .header {
    font-size: 60px;
    height: 84px;
    line-height: 84px;
    background: url('../../img/elements/buttonsGroup.png');
    background-image: -webkit-image-set(url('../../img/elements/buttonsGroup.webp') 1x, url('../../img/elements/buttonsGroup.png') 1x);
    background-repeat: no-repeat;
    background-position: 35% 0;
    margin-bottom: 0;
  }
}
